<template>
  <div>
    <v-app-bar color="dark accent-4" dense dark>
      <v-toolbar-title>Routers Handson</v-toolbar-title>
      <v-spacer></v-spacer>
      <button @click="navigate('/home', false)">
        <i class="fa fa-home" aria-hidden="true"></i>Home
      </button>
      <button v-if="isUserSessionActive" @click="navigate('/dashboard', false)">
        <i class="fa fa-tachometer" aria-hidden="true"></i>Dashboard
      </button>
      <button v-if="!isUserSessionActive" @click="navigate('/login', false)">
        <i class="fa fa-sign-in" aria-hidden="true"></i>Login
      </button>
      <button v-if="!isUserSessionActive" @click="navigate('/register', false)">
        <i class="fa fa-user-plus" aria-hidden="true"></i>Register
      </button>
      <button v-if="isUserSessionActive" @click="navigate('/home', true)">
        <i class="fa fa-sign-out" aria-hidden="true"></i>Logout
      </button>
    </v-app-bar>
  </div>
</template>
<script>
import { eventBus } from "../main";
export default {
  name: "Header",
  data: () => ({
    isUserSessionActive: false,
  }),
  created() {
    eventBus.$on("isUserSessionActive", (data) => {
      this.isUserSessionActive = data;
    });
  },
  methods: {
    navigate(path, isLogoutTriggered) {
      if (isLogoutTriggered) {
        eventBus.$emit("isUserSessionActive", false);
        localStorage.removeItem("UserID");
        path = "/home";
      }
      let currentRoute = this.$route.name;
      if ("/" + currentRoute.toLowerCase() !== path) this.$router.push(path);
    },
  },
};
</script>
<style scoped>
button {
  color: white;
  text-decoration: none;
  margin: 1%;
  padding: 10px;
  border: none;
  outline: none;
}
button:hover {
  cursor: pointer;
}
.active {
  background-color: white;
  border-radius: 10px;
  color: black;
}
</style>
