const Login = (resolve) => {
  require.ensure(
    ["../components/Login"],
    () => {
      resolve(require("../components/Login"));
    },
    "loginRegisterGroup"
  );
};

const Register = (resolve) => {
  require.ensure(
    ["../components/Register"],
    () => {
      resolve(require("../components/Register"));
    },
    "loginRegisterGroup"
  );
};
const Home = (resolve) => {
  require.ensure(["../components/Home"], () => {
    resolve(require("../components/Home"));
  });
};
const Dashboard = (resolve) => {
  require.ensure(["../components/Dashboard"], () => {
    resolve(require("../components/Dashboard"));
  });
};
const Error404 = (resolve) => {
  require.ensure(["../components/Error404"], () => {
    resolve(require("../components/Error404"));
  });
};

export const routes = [
  { path: "/home", component: Home, name: "Home" },
  { path: "", redirect: "Home" },
  { path: "/login", name: "Login", component: Login },
  { path: "/register", name: "Register", component: Register },
  { path: "/dashboard", name: "Dashboard", component: Dashboard },
  { path: "*", name: "Error404", component: Error404 },
];
